.login-container {
  width: 500px;
  padding: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  background: #2c2c35;
  color: #fff;
  border-radius: 10px;

  .heading {
    text-transform: uppercase;
    font-weight: 500;
  }

  .error-text {
    color: #d72424;
    font-size: .8em;
  }

  input[type="text"], input[type="password"] {
    background: none !important;
    display: block;
    margin: 20px auto;
    text-align: center;
    outline: none;
    border: 1px solid #15a2b8;
    border-radius: 4px;
    padding: 20px;
    color: #fff;
  }

  .login-btn {
    color: #fff;
    background-color: #2b2c35;
    border-color: #17a2b8;
    width: 200px;
    &:hover {
      color: red;
    }
  }
  .signup-text {
    font-size: .7em;
    padding: 10px;
    a:hover {
      text-decoration: none;
    }
  }
}
